.input {
   /* width: min-content; */
   display: flex;
    flex-direction: column;
    margin: 0 auto;
    max-width: 328px;
}

.icon {
   width: 30px;
   padding-bottom: 10px;
}

.head {
   text-align: center;
   padding: 0;
   color: white;

}

.firstName {
   display: flex;
   gap: 10px; /* Відступ між елементами */
   width: 1%; /* Забезпечити повний розмір контейнера */
