   font-size: 14px;

}


.button {
   width: 310px;
   padding: 10px;
   font-size: 14px;
   font-weight: 700;
   background-color: #08c0fa;
   color: #2b303a;
   border: 2px solid #08c0fa;
   border-radius: 5px;
   cursor: pointer;
}

.button:hover {
   background-color: transparent;
   color: white;
   transition: 0.4s;
}

